#ConnectModal {
  display: none;
  &.modal_open {
    display: block;
  }
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  outline: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.45);
  .modal_wrapper {
    border-radius: 4px;
    box-shadow: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08),
      0 9px 28px 8px rgba(0, 0, 0, 0.05);
    pointer-events: auto;
    color: var(--main-text-color);
    background: var(--main-background-color);
    width: 520px;
    margin: auto;
    margin-top: 100px;
  }
}
